/*------------------------------------------------------------
# Base
------------------------------------------------------------*/
/**
 * Set up a decent box model on the root element
 */
html {
  box-sizing: border-box;
}

/**
  * Make all elements from the DOM inherit from the parent box-sizing
  * Since `*` has a specificity of 0, it does not override the `html` value
  * making all elements inheriting from the root box-sizing value
  * See: https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/
  */
img,
iframe {
  max-width: 100%;
}

/* Elements
--------------------------------------------- */
/* Scroll */
::-webkit-scrollbar {
  height: 12px;
  width: 12px;
}
main ::-webkit-scrollbar {
  height: 8px;
  width: 8px;
}

::-webkit-scrollbar-thumb {
  background: #8A8A8A;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:active,
::-webkit-scrollbar-thumb:hover {
  background-color: #8A8A8A;
}

::-webkit-scrollbar-track {
  background: #fff;
}
main ::-webkit-scrollbar-track {
  background: #4D9EAD;
  border-radius: 5px;
  border: 2px solid transparent;
  background-clip: content-box;
}

::-webkit-scrollbar-track:hover::-webkit-scrollbar-track:hover,
::-webkit-scrollbar-track:active {
  background: #4D9EAD;
}

/* Selection */
::selection {
  background-color: #005D9F;
  color: #fff;
}

/* Selection */
::selection {
  background-color: #005D9F;
  color: #fff;
}

/*------------------------------------------------------------
# Layouts
------------------------------------------------------------*/
/* Navigation
--------------------------------------------- */
.site-header {
  background: #fff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 1.25rem 0;
}

.site-branding {
  display: inline-block;
  vertical-align: middle;
  max-width: 160px;
  width: 100%;
  height: auto;
}
.site-branding img, .site-branding .custom-logo {
  width: 100%;
  height: auto;
}
@media screen and (min-width: 992px) {
  .site-branding {
    width: 20%;
  }
}

.site-navigation {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  margin-top: 1rem;
}
.site-navigation .navigation-links {
  display: flex;
  justify-content: center;
}
.site-navigation .navigation-links a:not(:last-child) {
  margin-right: 2rem;
}
@media screen and (min-width: 992px) {
  .site-navigation {
    width: 80%;
    margin-top: 0;
  }
  .site-navigation .navigation-links {
    justify-content: flex-end;
  }
}

.main-navigation {
  display: block;
  width: 100%;
}
.main-navigation ul {
  display: none;
  list-style: none;
  margin: 0;
  padding-left: 0;
}
.main-navigation ul ul {
  background-color: #fff;
  margin-top: 0;
  width: 100%;
}
.main-navigation ul ul :hover > a,
.main-navigation ul ul .focus > a {
  color: #A9004C;
  text-decoration: underline;
}
.main-navigation ul li {
  position: relative;
  padding: 0.5rem 1rem;
}
.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
  display: block;
}
.main-navigation a {
  color: #585858;
  display: block;
  font-weight: 500;
  text-decoration: none;
  white-space: nowrap;
}
.main-navigation .current_page_item > a,
.main-navigation .current-menu-item > a,
.main-navigation .current_page_ancestor > a,
.main-navigation .current-menu-ancestor > a {
  font-weight: 700;
}
.main-navigation .menu-item-has-children {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  flex-wrap: nowrap;
}
.main-navigation .menu-item-has-children ul {
  background-color: #fff;
}
.main-navigation .menu-item-has-children ul a {
  margin-right: 0;
  position: relative;
}
.main-navigation .menu-item-has-children:after {
  content: "";
  background-image: url("data:image/svg+xml,%3Csvg width='14' height='9' viewBox='0 0 14 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7 8.5001L0 1.5001L1.4 0.100098L7 5.7001L12.6 0.100098L14 1.5001L7 8.5001Z' fill='%23585858'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  display: inline-block;
  height: 0.5rem;
  width: 1rem;
  position: absolute;
  top: 1.125rem;
  right: 0;
  margin-left: auto;
  margin-right: 0.5rem;
}
.main-navigation.toggled ul {
  display: block;
}
.main-navigation.toggled ul ul {
  display: none;
}
@media screen and (min-width: 992px) {
  .main-navigation ul {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-top: 1.25rem;
  }
  .main-navigation ul ul {
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    flex-direction: column;
    float: left;
    position: absolute;
    top: 100%;
    left: -999em;
    opacity: 0;
    transition: opacity 0.2s ease-in-out;
    width: auto;
    z-index: 99999;
  }
  .main-navigation ul ul ul {
    left: -999em;
    top: 0;
  }
  .main-navigation ul ul li {
    margin-right: 0 !important;
    padding: 0.5rem 1rem;
  }
  .main-navigation ul ul li:hover > ul, .main-navigation ul ul li.focus > ul {
    display: block;
    left: 100%;
    top: 0;
  }
  .main-navigation ul ul a {
    min-width: 140px;
  }
  .main-navigation ul li:not(:last-child) {
    margin-right: 3rem;
  }
  .main-navigation ul li {
    padding: 0;
  }
  .main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
    left: auto;
    top: 100%;
    transition: opacity 0.2s ease-in-out;
    opacity: 1;
  }
  .main-navigation .menu-item-has-children {
    align-items: center;
    flex-direction: row;
  }
  .main-navigation .menu-item-has-children:after {
    position: relative;
    top: unset;
    right: unset;
    margin-left: 0.5rem;
    margin-right: 0;
  }
  .main-navigation.toggled ul {
    display: flex;
    flex-direction: row;
  }
  .main-navigation.toggled ul ul {
    flex-direction: column;
  }
}

/* Small menu. */
.menu-toggle {
  display: block;
  background-color: transparent;
  border: 0;
}
@media screen and (min-width: 992px) {
  .menu-toggle {
    display: none;
  }
}

.site-main .comment-navigation,
.site-main .posts-navigation,
.site-main .post-navigation {
  margin: 0 0 1.5em;
}
.comment-navigation .nav-links,
.posts-navigation .nav-links,
.post-navigation .nav-links {
  display: flex;
}
.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
  flex: 1 0 50%;
}
.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
  text-align: end;
  flex: 1 0 50%;
}

/* Footer
--------------------------------------------- */
/*------------------------------------------------------------
# Components
------------------------------------------------------------*/
/* Posts and pages
--------------------------------------------- */
.sticky {
  display: block;
}

.post,
.page {
  margin: 0 0 1.5em;
}

.updated:not(.published) {
  display: none;
}

.hentry,
.page-content,
.entry-content,
.entry-summary {
  margin: 1.5em 0;
}

.page-links {
  clear: both;
  margin: 0 0 1.5em;
}

/* Comments
--------------------------------------------- */
.comment-content a {
  word-wrap: break-word;
}

.bypostauthor {
  display: block;
}

/* Media
--------------------------------------------- */
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
  display: inline-block;
}

